import React, { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import axios from 'axios';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Plus, Trash2, Edit, Shield, CheckCircle } from 'lucide-react';
import { toast } from 'sonner';
import CreateEmployeeDialog from '@/components/CreateEmployeeDialog';
import EditEmployeeDialog from '@/components/EditEmployeeDialog';
import PermissionsDialog from '@/components/PermissionsDialog';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';

export default function EmployeesPage() {
  const { getAuthHeader, API, user } = useAuth();
  const [employees, setEmployees] = useState([]);
  const [pendingUsers, setPendingUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showCreateDialog, setShowCreateDialog] = useState(false);
  const [editingEmployee, setEditingEmployee] = useState(null);
  const [managingPermissions, setManagingPermissions] = useState(null);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const [employeesRes, pendingRes] = await Promise.all([
        axios.get(`${API}/users`, { headers: getAuthHeader() }),
        axios.get(`${API}/users/pending`, { headers: getAuthHeader() }).catch(() => ({ data: [] }))
      ]);
      setEmployees(employeesRes.data);
      setPendingUsers(pendingRes.data || []);
    } catch (error) {
      toast.error('Failed to fetch employees');
    } finally {
      setLoading(false);
    }
  };

  const handleApprove = async (userId) => {
    try {
      await axios.post(`${API}/users/${userId}/approve`, {}, { headers: getAuthHeader() });
      toast.success('User approved successfully');
      fetchData();
    } catch (error) {
      toast.error('Failed to approve user');
    }
  };

  const handleDelete = async (employeeId) => {
    if (!window.confirm('Are you sure you want to delete this employee?')) return;
    
    try {
      await axios.delete(`${API}/users/${employeeId}`, { headers: getAuthHeader() });
      toast.success('Employee deleted successfully');
      fetchData();
    } catch (error) {
      toast.error(error.response?.data?.detail || 'Failed to delete employee');
    }
  };

  const getRoleBadge = (role) => {
    const colors = {
      admin: 'bg-red-100 text-red-800',
      manager: 'bg-blue-100 text-blue-800',
      member: 'bg-green-100 text-green-800'
    };
    return colors[role] || colors.member;
  };

  const isAdmin = user?.role === 'admin';

  if (loading) {
    return <div className="flex items-center justify-center h-96">Loading employees...</div>;
  }

  return (
    <div className="space-y-6" data-testid="employees-page">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-4xl font-bold text-gray-900 mb-2">Employees</h1>
          <p className="text-base text-muted-foreground">Manage team members and roles</p>
        </div>
        {isAdmin && (
          <Button onClick={() => setShowCreateDialog(true)} data-testid="create-employee-button">
            <Plus className="h-4 w-4 mr-2" />
            Add Employee
          </Button>
        )}
      </div>

      <Tabs defaultValue="approved" className="w-full">
        <TabsList>
          <TabsTrigger value="approved">Approved ({employees.length})</TabsTrigger>
          {isAdmin && <TabsTrigger value="pending">Pending Approval ({pendingUsers.length})</TabsTrigger>}
        </TabsList>

        <TabsContent value="approved" className="mt-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {employees.map((employee) => (
              <Card key={employee.id} className="hover:shadow-md transition-all" data-testid={`employee-card-${employee.id}`}>
                <CardHeader>
                  <div className="flex items-start justify-between">
                    <div className="flex items-center gap-3">
                      <div className="w-12 h-12 bg-primary rounded-full flex items-center justify-center text-white text-xl font-bold">
                        {employee.full_name.charAt(0)}
                      </div>
                      <div>
                        <CardTitle className="text-lg">{employee.full_name}</CardTitle>
                        <p className="text-sm text-muted-foreground">@{employee.username}</p>
                      </div>
                    </div>
                    <Badge className={getRoleBadge(employee.role)}>
                      {employee.role}
                    </Badge>
                  </div>
                </CardHeader>
                <CardContent>
                  <p className="text-sm text-muted-foreground mb-4">{employee.email}</p>
                  {isAdmin && employee.id !== 'admin-001' && (
                    <div className="flex gap-2">
                      <Button
                        variant="outline"
                        size="sm"
                        className="flex-1"
                        onClick={() => setManagingPermissions(employee)}
                        data-testid={`manage-permissions-${employee.id}`}
                      >
                        <Shield className="h-4 w-4 mr-2" />
                        Permissions
                      </Button>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => setEditingEmployee(employee)}
                        data-testid={`edit-employee-${employee.id}`}
                      >
                        <Edit className="h-4 w-4" />
                      </Button>
                      <Button
                        variant="destructive"
                        size="sm"
                        onClick={() => handleDelete(employee.id)}
                        data-testid={`delete-employee-${employee.id}`}
                      >
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    </div>
                  )}
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        {isAdmin && (
          <TabsContent value="pending" className="mt-6">
            {pendingUsers.length === 0 ? (
              <Card>
                <CardContent className="py-16 text-center">
                  <p className="text-muted-foreground">No pending approvals</p>
                </CardContent>
              </Card>
            ) : (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {pendingUsers.map((pendingUser) => (
                  <Card key={pendingUser.id} className="border-orange-200 bg-orange-50" data-testid={`pending-user-${pendingUser.id}`}>
                    <CardHeader>
                      <div className="flex items-center gap-3">
                        <div className="w-12 h-12 bg-orange-300 rounded-full flex items-center justify-center text-white text-xl font-bold">
                          {pendingUser.full_name.charAt(0)}
                        </div>
                        <div>
                          <CardTitle className="text-lg">{pendingUser.full_name}</CardTitle>
                          <p className="text-sm text-muted-foreground">@{pendingUser.username}</p>
                        </div>
                      </div>
                    </CardHeader>
                    <CardContent>
                      <p className="text-sm text-muted-foreground mb-4">{pendingUser.email}</p>
                      <Button
                        className="w-full"
                        onClick={() => handleApprove(pendingUser.id)}
                        data-testid={`approve-user-${pendingUser.id}`}
                      >
                        <CheckCircle className="h-4 w-4 mr-2" />
                        Approve User
                      </Button>
                    </CardContent>
                  </Card>
                ))}
              </div>
            )}
          </TabsContent>
        )}
      </Tabs>

      {showCreateDialog && (
        <CreateEmployeeDialog
          open={showCreateDialog}
          onClose={() => setShowCreateDialog(false)}
          onSuccess={() => {
            setShowCreateDialog(false);
            fetchData();
          }}
        />
      )}

      {editingEmployee && (
        <EditEmployeeDialog
          open={!!editingEmployee}
          employee={editingEmployee}
          onClose={() => setEditingEmployee(null)}
          onSuccess={() => {
            setEditingEmployee(null);
            fetchData();
          }}
        />
      )}

      {managingPermissions && (
        <PermissionsDialog
          open={!!managingPermissions}
          user={managingPermissions}
          onClose={() => setManagingPermissions(null)}
          onSuccess={() => {
            setManagingPermissions(null);
            fetchData();
          }}
        />
      )}
    </div>
  );
}
