import React, { useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import axios from 'axios';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { toast } from 'sonner';
import { Card, CardContent } from '@/components/ui/card';

export default function PermissionsDialog({ open, user, onClose, onSuccess }) {
  const { getAuthHeader, API } = useAuth();
  const [permissions, setPermissions] = useState(user?.page_permissions || {
    dashboard: { view: true, edit: false },
    tasks: { view: true, edit: false },
    weekly_planning: { view: true, edit: false },
    employees: { view: false, edit: false },
    settings: { view: false, edit: false }
  });
  const [loading, setLoading] = useState(false);

  const pages = [
    { key: 'dashboard', label: 'Dashboard' },
    { key: 'tasks', label: 'Tasks' },
    { key: 'weekly_planning', label: 'Weekly Planning' },
    { key: 'employees', label: 'Employees' },
    { key: 'settings', label: 'Settings' }
  ];

  const handlePermissionChange = (page, type, value) => {
    setPermissions(prev => ({
      ...prev,
      [page]: {
        ...prev[page],
        [type]: value
      }
    }));
  };

  const handleSubmit = async () => {
    setLoading(true);
    try {
      await axios.put(
        `${API}/users/${user.id}/permissions`,
        permissions,
        { headers: getAuthHeader() }
      );
      toast.success('Permissions updated successfully');
      onSuccess();
    } catch (error) {
      toast.error('Failed to update permissions');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="max-w-2xl" data-testid="permissions-dialog">
        <DialogHeader>
          <DialogTitle>Manage Permissions - {user?.full_name}</DialogTitle>
          <DialogDescription>Control page access and editing permissions</DialogDescription>
        </DialogHeader>
        <div className="space-y-4 max-h-[60vh] overflow-y-auto">
          {pages.map(page => (
            <Card key={page.key}>
              <CardContent className="pt-6">
                <div className="flex items-center justify-between mb-4">
                  <h4 className="font-semibold text-lg">{page.label}</h4>
                </div>
                <div className="grid grid-cols-2 gap-4">
                  <div className="flex items-center justify-between space-x-2 p-3 bg-gray-50 rounded-lg">
                    <Label htmlFor={`${page.key}-view`} className="cursor-pointer">
                      View Access
                    </Label>
                    <Switch
                      id={`${page.key}-view`}
                      checked={permissions[page.key]?.view || false}
                      onCheckedChange={(value) => handlePermissionChange(page.key, 'view', value)}
                      data-testid={`permission-${page.key}-view`}
                    />
                  </div>
                  <div className="flex items-center justify-between space-x-2 p-3 bg-gray-50 rounded-lg">
                    <Label htmlFor={`${page.key}-edit`} className="cursor-pointer">
                      Edit Access
                    </Label>
                    <Switch
                      id={`${page.key}-edit`}
                      checked={permissions[page.key]?.edit || false}
                      onCheckedChange={(value) => handlePermissionChange(page.key, 'edit', value)}
                      disabled={!permissions[page.key]?.view}
                      data-testid={`permission-${page.key}-edit`}
                    />
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
        <div className="flex justify-end gap-2 pt-4">
          <Button variant="outline" onClick={onClose}>Cancel</Button>
          <Button onClick={handleSubmit} disabled={loading} data-testid="save-permissions-button">
            {loading ? 'Saving...' : 'Save Permissions'}
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
