import React, { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import axios from 'axios';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Plus, Trash2, Save } from 'lucide-react';
import { toast } from 'sonner';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';

export default function SettingsPage() {
  const { getAuthHeader, API, user } = useAuth();
  const [priorities, setPriorities] = useState([]);
  const [teamTypes, setTeamTypes] = useState([]);
  const [loading, setLoading] = useState(true);
  const [newPriority, setNewPriority] = useState({ name: '', color: '#f97316', order: 1 });
  const [newTeamType, setNewTeamType] = useState({ name: '', color: '#3b82f6' });

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const [prioritiesRes, teamTypesRes] = await Promise.all([
        axios.get(`${API}/priorities`, { headers: getAuthHeader() }),
        axios.get(`${API}/team-types`, { headers: getAuthHeader() })
      ]);
      
      setPriorities(prioritiesRes.data);
      setTeamTypes(teamTypesRes.data);
    } catch (error) {
      toast.error('Failed to fetch settings');
    } finally {
      setLoading(false);
    }
  };

  const handleAddPriority = async () => {
    if (!newPriority.name.trim()) {
      toast.error('Priority name is required');
      return;
    }

    try {
      await axios.post(`${API}/priorities`, newPriority, { headers: getAuthHeader() });
      toast.success('Priority added successfully');
      setNewPriority({ name: '', color: '#f97316', order: priorities.length + 1 });
      fetchData();
    } catch (error) {
      toast.error('Failed to add priority');
    }
  };

  const handleDeletePriority = async (priorityId) => {
    if (!window.confirm('Are you sure you want to delete this priority?')) return;

    try {
      await axios.delete(`${API}/priorities/${priorityId}`, { headers: getAuthHeader() });
      toast.success('Priority deleted successfully');
      fetchData();
    } catch (error) {
      toast.error('Failed to delete priority');
    }
  };

  const handleAddTeamType = async () => {
    if (!newTeamType.name.trim()) {
      toast.error('Team type name is required');
      return;
    }

    try {
      await axios.post(`${API}/team-types`, newTeamType, { headers: getAuthHeader() });
      toast.success('Team type added successfully');
      setNewTeamType({ name: '', color: '#3b82f6' });
      fetchData();
    } catch (error) {
      toast.error('Failed to add team type');
    }
  };

  const handleDeleteTeamType = async (teamTypeId) => {
    if (!window.confirm('Are you sure you want to delete this team type?')) return;

    try {
      await axios.delete(`${API}/team-types/${teamTypeId}`, { headers: getAuthHeader() });
      toast.success('Team type deleted successfully');
      fetchData();
    } catch (error) {
      toast.error('Failed to delete team type');
    }
  };

  const isAdmin = user?.role === 'admin';

  if (loading) {
    return <div className="flex items-center justify-center h-96">Loading settings...</div>;
  }

  return (
    <div className="space-y-6" data-testid="settings-page">
      <div>
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Settings</h1>
        <p className="text-base text-muted-foreground">Manage priorities and team types</p>
      </div>

      <Tabs defaultValue="priorities" className="w-full">
        <TabsList className="grid w-full max-w-md grid-cols-2">
          <TabsTrigger value="priorities" data-testid="priorities-tab">Priorities</TabsTrigger>
          <TabsTrigger value="teamTypes" data-testid="team-types-tab">Team Types</TabsTrigger>
        </TabsList>

        <TabsContent value="priorities" className="space-y-6">
          <Card data-testid="priorities-card">
            <CardHeader>
              <CardTitle>Task Priorities</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {priorities.map((priority) => (
                <div
                  key={priority.id}
                  className="flex items-center justify-between p-4 border rounded-lg"
                  data-testid={`priority-item-${priority.id}`}
                >
                  <div className="flex items-center gap-3">
                    <div
                      className="w-6 h-6 rounded"
                      style={{ backgroundColor: priority.color }}
                    />
                    <span className="font-medium">{priority.name}</span>
                    <span className="text-sm text-muted-foreground">Order: {priority.order}</span>
                  </div>
                  {isAdmin && (
                    <Button
                      variant="destructive"
                      size="sm"
                      onClick={() => handleDeletePriority(priority.id)}
                      data-testid={`delete-priority-${priority.id}`}
                    >
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  )}
                </div>
              ))}

              {isAdmin && (
                <Card className="bg-orange-50 border-orange-200">
                  <CardContent className="pt-6 space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                      <div className="space-y-2">
                        <Label>Priority Name</Label>
                        <Input
                          placeholder="e.g., Urgent"
                          value={newPriority.name}
                          onChange={(e) => setNewPriority({ ...newPriority, name: e.target.value })}
                          data-testid="new-priority-name-input"
                        />
                      </div>
                      <div className="space-y-2">
                        <Label>Color</Label>
                        <Input
                          type="color"
                          value={newPriority.color}
                          onChange={(e) => setNewPriority({ ...newPriority, color: e.target.value })}
                          data-testid="new-priority-color-input"
                        />
                      </div>
                      <div className="space-y-2">
                        <Label>Order</Label>
                        <Input
                          type="number"
                          value={newPriority.order}
                          onChange={(e) => setNewPriority({ ...newPriority, order: parseInt(e.target.value) || 1 })}
                          data-testid="new-priority-order-input"
                        />
                      </div>
                    </div>
                    <Button onClick={handleAddPriority} className="w-full" data-testid="add-priority-button">
                      <Plus className="h-4 w-4 mr-2" />
                      Add Priority
                    </Button>
                  </CardContent>
                </Card>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="teamTypes" className="space-y-6">
          <Card data-testid="team-types-card">
            <CardHeader>
              <CardTitle>Team Types</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {teamTypes.map((teamType) => (
                <div
                  key={teamType.id}
                  className="flex items-center justify-between p-4 border rounded-lg"
                  data-testid={`team-type-item-${teamType.id}`}
                >
                  <div className="flex items-center gap-3">
                    <div
                      className="w-6 h-6 rounded"
                      style={{ backgroundColor: teamType.color }}
                    />
                    <span className="font-medium">{teamType.name}</span>
                  </div>
                  {isAdmin && (
                    <Button
                      variant="destructive"
                      size="sm"
                      onClick={() => handleDeleteTeamType(teamType.id)}
                      data-testid={`delete-team-type-${teamType.id}`}
                    >
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  )}
                </div>
              ))}

              {isAdmin && (
                <Card className="bg-orange-50 border-orange-200">
                  <CardContent className="pt-6 space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="space-y-2">
                        <Label>Team Type Name</Label>
                        <Input
                          placeholder="e.g., Design"
                          value={newTeamType.name}
                          onChange={(e) => setNewTeamType({ ...newTeamType, name: e.target.value })}
                          data-testid="new-team-type-name-input"
                        />
                      </div>
                      <div className="space-y-2">
                        <Label>Color</Label>
                        <Input
                          type="color"
                          value={newTeamType.color}
                          onChange={(e) => setNewTeamType({ ...newTeamType, color: e.target.value })}
                          data-testid="new-team-type-color-input"
                        />
                      </div>
                    </div>
                    <Button onClick={handleAddTeamType} className="w-full" data-testid="add-team-type-button">
                      <Plus className="h-4 w-4 mr-2" />
                      Add Team Type
                    </Button>
                  </CardContent>
                </Card>
              )}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}
