import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useAuth } from '@/context/AuthContext';
import axios from 'axios';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Checkbox } from '@/components/ui/checkbox';
import { ArrowLeft, Save, Plus, Trash2, Upload, Download, Clock } from 'lucide-react';
import { toast } from 'sonner';
import { format } from 'date-fns';

export default function TaskDetailPage() {
  const { taskId } = useParams();
  const navigate = useNavigate();
  const { getAuthHeader, API, user } = useAuth();
  const [task, setTask] = useState(null);
  const [users, setUsers] = useState([]);
  const [priorities, setPriorities] = useState([]);
  const [teamTypes, setTeamTypes] = useState([]);
  const [loading, setLoading] = useState(true);
  const [editing, setEditing] = useState(false);
  const [newSubtask, setNewSubtask] = useState('');

  useEffect(() => {
    fetchData();
  }, [taskId]);

  const fetchData = async () => {
    try {
      const [taskRes, usersRes, prioritiesRes, teamTypesRes] = await Promise.all([
        axios.get(`${API}/tasks/${taskId}`, { headers: getAuthHeader() }),
        axios.get(`${API}/users`, { headers: getAuthHeader() }),
        axios.get(`${API}/priorities`, { headers: getAuthHeader() }),
        axios.get(`${API}/team-types`, { headers: getAuthHeader() })
      ]);
      
      setTask(taskRes.data);
      setUsers(usersRes.data);
      setPriorities(prioritiesRes.data);
      setTeamTypes(teamTypesRes.data);
    } catch (error) {
      toast.error('Failed to fetch task details');
    } finally {
      setLoading(false);
    }
  };

  const handleUpdate = async () => {
    try {
      await axios.put(
        `${API}/tasks/${taskId}`,
        {
          title: task.title,
          description: task.description,
          status: task.status,
          priorityId: task.priorityId,
          teamTypeId: task.teamTypeId,
          assignedTo: task.assignedTo,
          dueDate: task.dueDate,
          time_taken: task.time_taken
        },
        { headers: getAuthHeader() }
      );
      toast.success('Task updated successfully');
      setEditing(false);
      fetchData();
    } catch (error) {
      toast.error('Failed to update task');
    }
  };

  const handleAddSubtask = async () => {
    if (!newSubtask.trim()) return;
    
    try {
      await axios.post(
        `${API}/tasks/${taskId}/subtasks`,
        { title: newSubtask },
        { headers: getAuthHeader() }
      );
      toast.success('Subtask added');
      setNewSubtask('');
      fetchData();
    } catch (error) {
      toast.error('Failed to add subtask');
    }
  };

  const handleToggleSubtask = async (subtaskId, completed) => {
    try {
      await axios.put(
        `${API}/tasks/${taskId}/subtasks/${subtaskId}?completed=${!completed}`,
        {},
        { headers: getAuthHeader() }
      );
      fetchData();
    } catch (error) {
      toast.error('Failed to update subtask');
    }
  };

  const handleDeleteSubtask = async (subtaskId) => {
    try {
      await axios.delete(
        `${API}/tasks/${taskId}/subtasks/${subtaskId}`,
        { headers: getAuthHeader() }
      );
      toast.success('Subtask deleted');
      fetchData();
    } catch (error) {
      toast.error('Failed to delete subtask');
    }
  };

  const handleFileUpload = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const formData = new FormData();
    formData.append('file', file);

    try {
      await axios.post(
        `${API}/tasks/${taskId}/attachments`,
        formData,
        { 
          headers: { 
            ...getAuthHeader(),
            'Content-Type': 'multipart/form-data'
          }
        }
      );
      toast.success('File uploaded successfully');
      fetchData();
    } catch (error) {
      toast.error('Failed to upload file');
    }
  };

  const handleDownloadAttachment = (attachment) => {
    const byteCharacters = atob(attachment.data);
    const byteNumbers = new Array(byteCharacters.length);
    for (let i = 0; i < byteCharacters.length; i++) {
      byteNumbers[i] = byteCharacters.charCodeAt(i);
    }
    const byteArray = new Uint8Array(byteNumbers);
    const blob = new Blob([byteArray]);
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = attachment.filename;
    a.click();
  };

  const canEdit = user?.role === 'admin' || user?.role === 'manager' || task?.assignedTo === user?.id;

  if (loading) {
    return <div className="flex items-center justify-center h-96">Loading task...</div>;
  }

  if (!task) {
    return <div className="flex items-center justify-center h-96">Task not found</div>;
  }

  return (
    <div className="space-y-6" data-testid="task-detail-page">
      <div className="flex items-center justify-between">
        <Button variant="ghost" onClick={() => navigate('/tasks')} data-testid="back-button">
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Tasks
        </Button>
        {canEdit && (
          <div className="flex gap-2">
            {editing ? (
              <>
                <Button variant="outline" onClick={() => setEditing(false)}>Cancel</Button>
                <Button onClick={handleUpdate} data-testid="save-task-button">
                  <Save className="h-4 w-4 mr-2" />
                  Save Changes
                </Button>
              </>
            ) : (
              <Button onClick={() => setEditing(true)} data-testid="edit-task-button">Edit Task</Button>
            )}
          </div>
        )}
      </div>

      <Card data-testid="task-detail-card">
        <CardHeader>
          <CardTitle>
            {editing ? (
              <Input
                value={task.title}
                onChange={(e) => setTask({ ...task, title: e.target.value })}
                className="text-2xl font-bold"
                data-testid="task-title-input"
              />
            ) : (
              <span className="text-3xl">{task.title}</span>
            )}
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label>Status</Label>
              {editing ? (
                <Select value={task.status} onValueChange={(value) => setTask({ ...task, status: value })}>
                  <SelectTrigger data-testid="status-select">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="todo">To Do</SelectItem>
                    <SelectItem value="in_progress">In Progress</SelectItem>
                    <SelectItem value="completed">Completed</SelectItem>
                    <SelectItem value="blocked">Blocked</SelectItem>
                  </SelectContent>
                </Select>
              ) : (
                <Badge className="text-base py-1 px-3">{task.status.replace('_', ' ')}</Badge>
              )}
            </div>

            <div className="space-y-2">
              <Label>Assigned To</Label>
              {editing ? (
                <Select value={task.assignedTo || ''} onValueChange={(value) => setTask({ ...task, assignedTo: value })}>
                  <SelectTrigger data-testid="assigned-to-select">
                    <SelectValue placeholder="Select user" />
                  </SelectTrigger>
                  <SelectContent>
                    {users.map(u => (
                      <SelectItem key={u.id} value={u.id}>{u.full_name}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              ) : (
                <p>{users.find(u => u.id === task.assignedTo)?.full_name || 'Unassigned'}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label>Priority</Label>
              {editing ? (
                <Select value={task.priorityId || ''} onValueChange={(value) => setTask({ ...task, priorityId: value })}>
                  <SelectTrigger data-testid="priority-select">
                    <SelectValue placeholder="Select priority" />
                  </SelectTrigger>
                  <SelectContent>
                    {priorities.map(p => (
                      <SelectItem key={p.id} value={p.id}>{p.name}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              ) : (
                <p>{priorities.find(p => p.id === task.priorityId)?.name || 'None'}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label>Team Type</Label>
              {editing ? (
                <Select value={task.teamTypeId || ''} onValueChange={(value) => setTask({ ...task, teamTypeId: value })}>
                  <SelectTrigger data-testid="team-type-select">
                    <SelectValue placeholder="Select team type" />
                  </SelectTrigger>
                  <SelectContent>
                    {teamTypes.map(t => (
                      <SelectItem key={t.id} value={t.id}>{t.name}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              ) : (
                <p>{teamTypes.find(t => t.id === task.teamTypeId)?.name || 'None'}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label>Due Date</Label>
              {editing ? (
                <Input
                  type="date"
                  value={task.dueDate ? task.dueDate.split('T')[0] : ''}
                  onChange={(e) => setTask({ ...task, dueDate: e.target.value })}
                  data-testid="due-date-input"
                />
              ) : (
                <p>{task.dueDate ? format(new Date(task.dueDate), 'MMM dd, yyyy') : 'Not set'}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label className="flex items-center gap-2">
                <Clock className="h-4 w-4" />
                Time Taken (minutes)
              </Label>
              {editing ? (
                <Input
                  type="number"
                  value={task.time_taken}
                  onChange={(e) => setTask({ ...task, time_taken: parseInt(e.target.value) || 0 })}
                  data-testid="time-taken-input"
                />
              ) : (
                <p>{task.time_taken} minutes</p>
              )}
            </div>
          </div>

          <div className="space-y-2">
            <Label>Description</Label>
            {editing ? (
              <Textarea
                value={task.description || ''}
                onChange={(e) => setTask({ ...task, description: e.target.value })}
                rows={4}
                data-testid="description-textarea"
              />
            ) : (
              <p className="text-muted-foreground">{task.description || 'No description'}</p>
            )}
          </div>
        </CardContent>
      </Card>

      <Card data-testid="subtasks-card">
        <CardHeader>
          <CardTitle>Subtasks</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          {task.subtasks && task.subtasks.map((subtask) => (
            <div key={subtask.id} className="flex items-center justify-between p-3 bg-orange-50 rounded-lg" data-testid={`subtask-${subtask.id}`}>
              <div className="flex items-center gap-3">
                <Checkbox
                  checked={subtask.completed}
                  onCheckedChange={() => handleToggleSubtask(subtask.id, subtask.completed)}
                  data-testid={`subtask-checkbox-${subtask.id}`}
                />
                <span className={subtask.completed ? 'line-through text-muted-foreground' : ''}>
                  {subtask.title}
                </span>
              </div>
              {canEdit && (
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => handleDeleteSubtask(subtask.id)}
                  data-testid={`delete-subtask-${subtask.id}`}
                >
                  <Trash2 className="h-4 w-4 text-red-500" />
                </Button>
              )}
            </div>
          ))}

          {canEdit && (
            <div className="flex gap-2">
              <Input
                placeholder="Add a subtask..."
                value={newSubtask}
                onChange={(e) => setNewSubtask(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handleAddSubtask()}
                data-testid="new-subtask-input"
              />
              <Button onClick={handleAddSubtask} data-testid="add-subtask-button">
                <Plus className="h-4 w-4" />
              </Button>
            </div>
          )}
        </CardContent>
      </Card>

      <Card data-testid="attachments-card">
        <CardHeader>
          <CardTitle>Attachments</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          {task.attachments && task.attachments.map((attachment) => (
            <div key={attachment.id} className="flex items-center justify-between p-3 bg-orange-50 rounded-lg" data-testid={`attachment-${attachment.id}`}>
              <div>
                <p className="font-medium">{attachment.filename}</p>
                <p className="text-sm text-muted-foreground">
                  Uploaded by {attachment.uploaded_by} on {format(new Date(attachment.uploaded_at), 'MMM dd, yyyy')}
                </p>
              </div>
              <Button
                variant="outline"
                size="sm"
                onClick={() => handleDownloadAttachment(attachment)}
                data-testid={`download-attachment-${attachment.id}`}
              >
                <Download className="h-4 w-4" />
              </Button>
            </div>
          ))}

          {canEdit && (
            <div>
              <Input
                type="file"
                onChange={handleFileUpload}
                className="cursor-pointer"
                data-testid="upload-file-input"
              />
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
