import React, { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import axios from 'axios';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Plus, Calendar, User, Flag } from 'lucide-react';
import { toast } from 'sonner';
import CreateTaskDialog from '@/components/CreateTaskDialog';
import { useNavigate } from 'react-router-dom';
import { format } from 'date-fns';

export default function TasksPage() {
  const { getAuthHeader, API, user } = useAuth();
  const [tasks, setTasks] = useState([]);
  const [users, setUsers] = useState([]);
  const [priorities, setPriorities] = useState([]);
  const [teamTypes, setTeamTypes] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showCreateDialog, setShowCreateDialog] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const [tasksRes, usersRes, prioritiesRes, teamTypesRes] = await Promise.all([
        axios.get(`${API}/tasks`, { headers: getAuthHeader() }),
        axios.get(`${API}/users`, { headers: getAuthHeader() }),
        axios.get(`${API}/priorities`, { headers: getAuthHeader() }),
        axios.get(`${API}/team-types`, { headers: getAuthHeader() })
      ]);
      
      setTasks(tasksRes.data);
      setUsers(usersRes.data);
      setPriorities(prioritiesRes.data);
      setTeamTypes(teamTypesRes.data);
    } catch (error) {
      toast.error('Failed to fetch tasks');
    } finally {
      setLoading(false);
    }
  };

  const getStatusColor = (status) => {
    const colors = {
      todo: 'bg-gray-100 text-gray-800',
      in_progress: 'bg-blue-100 text-blue-800',
      completed: 'bg-green-100 text-green-800',
      blocked: 'bg-red-100 text-red-800'
    };
    return colors[status] || colors.todo;
  };

  const getStatusLabel = (status) => {
    const labels = {
      todo: 'To Do',
      in_progress: 'In Progress',
      completed: 'Completed',
      blocked: 'Blocked'
    };
    return labels[status] || status;
  };

  const getUserName = (userId) => {
    const user = users.find(u => u.id === userId);
    return user ? user.full_name : 'Unassigned';
  };

  const getPriorityName = (priorityId) => {
    const priority = priorities.find(p => p.id === priorityId);
    return priority ? priority.name : null;
  };

  const getPriorityColor = (priorityId) => {
    const priority = priorities.find(p => p.id === priorityId);
    return priority ? priority.color : '#6b7280';
  };

  const canCreateTask = user?.role === 'admin' || user?.role === 'manager';

  if (loading) {
    return <div className="flex items-center justify-center h-96">Loading tasks...</div>;
  }

  return (
    <div className="space-y-6" data-testid="tasks-page">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-4xl font-bold text-gray-900 mb-2">Tasks</h1>
          <p className="text-base text-muted-foreground">Manage and track all tasks</p>
        </div>
        {canCreateTask && (
          <Button 
            onClick={() => setShowCreateDialog(true)}
            data-testid="create-task-button"
            className="gap-2"
          >
            <Plus className="h-4 w-4" />
            Create Task
          </Button>
        )}
      </div>

      <div className="grid grid-cols-1 gap-4">
        {tasks.length === 0 ? (
          <Card>
            <CardContent className="flex flex-col items-center justify-center py-16">
              <p className="text-muted-foreground text-lg mb-4">No tasks yet</p>
              {canCreateTask && (
                <Button onClick={() => setShowCreateDialog(true)}>Create your first task</Button>
              )}
            </CardContent>
          </Card>
        ) : (
          tasks.map((task) => (
            <Card 
              key={task.id} 
              className="hover:shadow-md transition-all cursor-pointer border-l-4" 
              style={{ borderLeftColor: task.priorityId ? getPriorityColor(task.priorityId) : '#6b7280' }}
              onClick={() => navigate(`/tasks/${task.id}`)}
              data-testid={`task-card-${task.id}`}
            >
              <CardHeader className="pb-3">
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <CardTitle className="text-xl mb-2">{task.title}</CardTitle>
                    {task.description && (
                      <p className="text-sm text-muted-foreground line-clamp-2">{task.description}</p>
                    )}
                  </div>
                  <Badge className={getStatusColor(task.status)} data-testid={`task-status-${task.id}`}>
                    {getStatusLabel(task.status)}
                  </Badge>
                </div>
              </CardHeader>
              <CardContent>
                <div className="flex flex-wrap gap-4 text-sm">
                  {task.assignedTo && (
                    <div className="flex items-center gap-1 text-muted-foreground">
                      <User className="h-4 w-4" />
                      <span>{getUserName(task.assignedTo)}</span>
                    </div>
                  )}
                  {task.priorityId && (
                    <div className="flex items-center gap-1" style={{ color: getPriorityColor(task.priorityId) }}>
                      <Flag className="h-4 w-4" />
                      <span className="font-medium">{getPriorityName(task.priorityId)}</span>
                    </div>
                  )}
                  {task.dueDate && (
                    <div className="flex items-center gap-1 text-muted-foreground">
                      <Calendar className="h-4 w-4" />
                      <span>{format(new Date(task.dueDate), 'MMM dd, yyyy')}</span>
                    </div>
                  )}
                  {task.subtasks && task.subtasks.length > 0 && (
                    <div className="flex items-center gap-1 text-muted-foreground">
                      <span>{task.subtasks.filter(s => s.completed).length}/{task.subtasks.length} subtasks</span>
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          ))
        )}
      </div>

      {showCreateDialog && (
        <CreateTaskDialog
          open={showCreateDialog}
          onClose={() => setShowCreateDialog(false)}
          onSuccess={() => {
            setShowCreateDialog(false);
            fetchData();
          }}
          users={users}
          priorities={priorities}
          teamTypes={teamTypes}
        />
      )}
    </div>
  );
}
