import React, { useEffect, useMemo, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import axios from 'axios';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Calendar, User as UserIcon } from 'lucide-react';
import { toast } from 'sonner';
import { useNavigate } from 'react-router-dom';

export default function WeeklyPlanningPage() {
  const { getAuthHeader, API } = useAuth();
  const [weeklyData, setWeeklyData] = useState({});
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    fetchWeeklyPlanning();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetchWeeklyPlanning = async () => {
    try {
      const response = await axios.get(`${API}/weekly-planning`, {
        headers: getAuthHeader(),
      });
      setWeeklyData(response.data || {});
    } catch (error) {
      toast.error('Failed to fetch weekly planning');
    } finally {
      setLoading(false);
    }
  };

  const getStatusColor = (status) => {
    const colors = {
      todo: 'bg-gray-100 text-gray-800',
      in_progress: 'bg-blue-100 text-blue-800',
      completed: 'bg-green-100 text-green-800',
      blocked: 'bg-red-100 text-red-800',
    };
    return colors[status] || colors.todo;
  };

  const getAssigneeLabel = (assignedTo) => {
    // supports: string, {name}, {fullName}, {username}, {email}
    if (!assignedTo) return null;
    if (typeof assignedTo === 'string') return assignedTo;
    return (
      assignedTo.full_name ||
      assignedTo.name ||
      assignedTo.username ||
      assignedTo.email ||
      null
    );
  };

  const days = useMemo(
    () => ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
    []
  );

  if (loading) {
    return (
      <div className="flex items-center justify-center h-96">
        Loading weekly planning...
      </div>
    );
  }

  return (
    <div className="space-y-6" data-testid="weekly-planning-page">
      <div className="flex items-center gap-3">
        <Calendar className="h-8 w-8 text-primary" />
        <div>
          <h1 className="text-4xl font-bold text-gray-900">Weekly Planning</h1>
          <p className="text-base text-muted-foreground">Kanban view of tasks by day</p>
        </div>
      </div>

      <div className="flex gap-6 overflow-x-auto pb-8 snap-x snap-mandatory" data-testid="kanban-board">
        {days.map((day) => {
          const dayTasks = weeklyData?.[day] || [];

          return (
            <div key={day} className="flex-shrink-0 w-80 snap-start" data-testid={`kanban-column-${day}`}>
              <Card className="h-full border-orange-100">
                <CardHeader className="bg-orange-50 border-b border-orange-100">
                  <CardTitle className="flex items-center justify-between">
                    <span>{day}</span>
                    <Badge variant="secondary" className="ml-2">
                      {dayTasks.length}
                    </Badge>
                  </CardTitle>
                </CardHeader>

                <CardContent className="p-4 space-y-3 min-h-[500px]">
                  {dayTasks.length === 0 ? (
                    <p className="text-sm text-muted-foreground text-center py-8">
                      No tasks for {day}
                    </p>
                  ) : (
                    dayTasks.map((task) => {
                      const assignee = getAssigneeLabel(task.assignedTo);

                      return (
                        <Card
                          key={task.id}
                          className="cursor-pointer hover:shadow-md transition-all"
                          onClick={() => navigate(`/tasks/${task.id}`)}
                          data-testid={`kanban-task-${task.id}`}
                        >
                          <CardContent className="p-3 space-y-2">
                            <div className="flex items-start justify-between gap-2">
                              <h4 className="font-semibold text-sm line-clamp-2">{task.title}</h4>
                              {assignee && (
                                <Badge variant="outline" className="text-[11px] shrink-0 flex items-center gap-1">
                                  <UserIcon className="h-3.5 w-3.5" />
                                  <span className="line-clamp-1 max-w-[120px]">{assignee}</span>
                                </Badge>
                              )}
                            </div>

                            {task.description && (
                              <p className="text-xs text-muted-foreground line-clamp-2">
                                {task.description}
                              </p>
                            )}

                            <div className="flex items-center gap-2 flex-wrap">
                              <Badge className={`text-xs ${getStatusColor(task.status)}`}>
                                {String(task.status || 'todo').replace('_', ' ')}
                              </Badge>

                              {/* Optional: show team/priority if present */}
                              {task.priority?.name && (
                                <Badge variant="secondary" className="text-xs">
                                  {task.priority.name}
                                </Badge>
                              )}
                            </div>
                          </CardContent>
                        </Card>
                      );
                    })
                  )}
                </CardContent>
              </Card>
            </div>
          );
        })}
      </div>
    </div>
  );
}