import React, { useState, useEffect } from 'react';
import { useAuth } from '@/context/AuthContext';
import axios from 'axios';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { toast } from 'sonner';

export default function EditEmployeeDialog({ open, employee, onClose, onSuccess }) {
  const { getAuthHeader, API } = useAuth();
  const [updatedEmployee, setUpdatedEmployee] = useState({
    username: '',
    email: '',
    full_name: '',
    role: 'member'
  });
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (employee) {
      setUpdatedEmployee({
        username: employee.username,
        email: employee.email,
        full_name: employee.full_name,
        role: employee.role
      });
    }
  }, [employee]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      await axios.put(`${API}/users/${employee.id}`, updatedEmployee, { headers: getAuthHeader() });
      toast.success('Employee updated successfully');
      onSuccess();
    } catch (error) {
      toast.error('Failed to update employee');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent data-testid="edit-employee-dialog">
        <DialogHeader>
          <DialogTitle>Edit Employee</DialogTitle>
          <DialogDescription>Update employee information</DialogDescription>
        </DialogHeader>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="full_name">Full Name *</Label>
            <Input
              id="full_name"
              data-testid="edit-employee-full-name-input"
              placeholder="Enter full name"
              value={updatedEmployee.full_name}
              onChange={(e) => setUpdatedEmployee({ ...updatedEmployee, full_name: e.target.value })}
              required
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="username">Username *</Label>
            <Input
              id="username"
              data-testid="edit-employee-username-input"
              placeholder="Enter username"
              value={updatedEmployee.username}
              onChange={(e) => setUpdatedEmployee({ ...updatedEmployee, username: e.target.value })}
              required
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="email">Email *</Label>
            <Input
              id="email"
              data-testid="edit-employee-email-input"
              type="email"
              placeholder="Enter email"
              value={updatedEmployee.email}
              onChange={(e) => setUpdatedEmployee({ ...updatedEmployee, email: e.target.value })}
              required
            />
          </div>

          <div className="space-y-2">
            <Label>Role</Label>
            <Select value={updatedEmployee.role} onValueChange={(value) => setUpdatedEmployee({ ...updatedEmployee, role: value })}>
              <SelectTrigger data-testid="edit-employee-role-select">
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="admin">Admin</SelectItem>
                <SelectItem value="manager">Manager</SelectItem>
                <SelectItem value="member">Member</SelectItem>
              </SelectContent>
            </Select>
          </div>

          <div className="flex justify-end gap-2 pt-4">
            <Button type="button" variant="outline" onClick={onClose}>Cancel</Button>
            <Button type="submit" disabled={loading} data-testid="submit-edit-employee">
              {loading ? 'Updating...' : 'Update Employee'}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
